import s from './Footer.module.css'
import {NavLink} from "react-router-dom";

const Footer = (props) => {
    return (
        <div className={s.footer}>
            <div className={s.creator}>
                <NavLink to='/profile/15537'>Sergey Karpov</NavLink>
            </div>
            <div className={s.contactsCreator}>
                <span className={s.telegram}>
                    <a target="_blank" href="https://t.me/sergey_karpov31">Telegram </a>
                </span>
                <span className={s.instagram}>
                    <a target="_blank" href="https://www.instagram.com/sergey_karpov_/">Instagram</a>
                </span>
            </div>
            <div className={s.copyright}>
                © 2021
            </div>
        </div>
    )
}

export default Footer